import * as React from 'react';
import './App.css';

import Login from './containers/login_container';
import MainLayout from './components/layout_component';
import { NavBar, NavBarColor } from './components/navbar_component';

class App extends React.Component {
  public render() {
    return (
      <div className="App">
        <NavBar color={NavBarColor.BLUE} />
        <MainLayout>
          <Login />
        </MainLayout>
      </div>
    );
  }
}

export default App;



