import { createStore, combineReducers } from 'redux';
import { LoginState, loginReducer } from './reducers/login_reducer';

export interface AppState {
  login: LoginState;
}

const store = createStore(
  combineReducers<AppState>({
    login: loginReducer,
  })
);

export default store;



