import * as React from 'react';
import { LoginState } from '../reducers/login_reducer';
import { UpdateUserNameAction, UpdatePasswordAction } from '../actions/login_action';

interface LoginDispatcher {
  updateUserName: (username: string) => UpdateUserNameAction;
  updatePassword: (password: string) => UpdatePasswordAction;
}

export type LoginProps = LoginState & LoginDispatcher;

export const LoginComponent: React.SFC<LoginProps> = (props: LoginProps) => {
  return (
    <div className="row">
      <form className="col s12">
        <div className="row">
          <div className="input-field col s12">
            <input
              id="username"
              type="text"
              value={props.username}
              onChange={e => props.updateUserName(e.target.value)}
            />
            <label htmlFor="username">ユーザ名</label>
          </div>
        </div>
        <div className="row">
          <div className="input-field col s12">
            <input
              id="password"
              type="password"
              value={props.password}
              onChange={e => props.updatePassword(e.target.value)}
            />
            <label htmlFor="password">パスワード</label>
          </div>
        </div>
        <input type="submit" value="送信" className="col s4 waves-effect waves-light btn" />
      </form>
    </div>
  );
};



